<template lang="pug">
    .department__item-content
        .department__info
            .department__code Департамент: {{department.name}}
            .buttons__departments
                button(@click.prevent='departmentExpandEvent()' v-if='!departmentVisible' :id='department.id').department__view-expand
                button(@click.prevent='departmentHideEvent()' v-if='departmentVisible').department__view-hide
        department-item-container(
            v-if='departmentVisible'
            :department='department'
        )

</template>
<script>
export default {
    components: {
        departmentItemContainer: () => import("../../admin/departments/department-item-container")
    },
    data() {
        return {
            departmentVisible: false 
        }
    },
    props: {
        department: Object
    },
    methods: {
        departmentExpandEvent(){
            this.departmentVisible=!this.departmentVisible
            let buttons = document.getElementsByClassName('department__view-expand')
            buttons.forEach(elem => {
                elem.disabled=true
            });
            document.getElementById(this.department.id).removeAttribute('disabled')
        },
        departmentHideEvent() {
            this.departmentVisible=!this.departmentVisible
            let buttons = document.getElementsByClassName('department__view-expand')
            buttons.forEach(elem => {
                elem.disabled=false
            });
        }
    }
}
</script>
<style scoped lang="scss">
.department__item-content {
    display: flex;
    flex-direction: column;
    flex: 1;
    position: relative;
}
.department__info {
    padding-bottom: 5px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.department__code {
    font-family: Alegreya Sans Bold;
    margin-right: 3%;
    width: 50%;
}
.buttons__departments {
    display: flex;
}
.department__view-expand {
    background-color:#464549;
    width: 25px;
    height: 25px;
    border-radius: 5px;
    position: relative;
    &:hover {
        opacity: .8;
        transition: .5s;
        cursor: pointer;
    }
    &:before {
        content: '';
        display: block;
        width: 2px;
        height: 11px;
        background: #faf4e8;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    &:after {
        content: '';
        display: block;
        width: 2px;
        height: 11px;
        background: #faf4e8;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) rotate(90deg);
    }
}
.department__view-hide {
    background-color:#464549;
    width: 25px;
    height: 25px;
    border-radius: 5px;
    margin: auto;
    &:hover {
        opacity: .8;
        transition: .5s;
        cursor: pointer;
    }
    &:after {
        content: '';
        display: block;
        width: 11px;
        border-radius: 2px;
        height: 2px;
        background: #faf4e8;
        background-size: cover;
    }
}
</style>